  //   //console.log("in finditem", items, text);
  //   for (var i = 0; i < items.length; i++) {
  //     var item = items[i];
  //     console.log("in for", items[i]);
  //     //console.log("i am if", item);
  //     console.log("item.key", item.header, text);
  //     console.log("item.items", item.items);
  //     if (item.key === text) {
  //       return item;
